@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Demo {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.Task {
    font-family: 'Roboto', sans-serif;
    font-size: 120px;
    display: flex;
    align-items: center;
}

.Task-wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin: 20px;
}

.Task input {
    border-color: rgba(0, 0, 0, .39);
    border-radius: 35px;
    background: rgba(196, 196, 196, .26);
    width: 150px;
    height: 118px;
    font-size: 90px;
    box-sizing: border-box;
    padding: 20px;
    margin-left: 20px;
}

.Task .Task_input_correct {
    background: rgba(73, 255, 80, .26);
    border-color: rgba(134, 171, 136, .39);
    color: #028D3A;
}

.Task .Task_input_wrong {
    background: rgba(255, 109, 111, 0.26);
    border-color: rgba(134, 171, 136, .39);
    color: #8d0b27;
}

.Task_button {
    background: rgba(251, 138, 56, .91);
    border-radius: 60px;
    font-size: 24px;
    border: 0;
    color: #fff;
    padding: 24px 190px;
    margin-top: 40px;
    cursor: pointer;
}

.Timer {
    background: url(/ed-tech-task/static/media/timer-back.d201cae9.png);
    width: 537px;
    height: 274px;
    font-family: 'Roboto Mono', monospace;
    position: relative;
}

.Timer_digits {
    position: absolute;
    top: 33%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: #111111;
    font-size: 96px;
}

.Timer_answer {
    color: #028D3A;
    font-size: 36px;
    align-self: flex-start;
    margin-top: 65px;
}

.Timer_answer_wrong {
    color: #8d0b27;
    font-size: 36px;
    align-self: flex-start;
    margin-top: 65px;
}

.VisualExample {
    margin-top: 60px;
}

.VisualExample_holder {
    display: flex;
}

.VisualExample_pics {
    position: relative;
}

.VisualExample_demo {
    display: inline-flex;
    flex-direction: column;
    justify-items: center;
    align-items: flex-start;
}

.VisualExample_demo_answer {
    display: inline-flex;
    flex-direction: column;
    justify-items: center;
    align-items: flex-start;
    position: absolute;
    right: 0;
    bottom: 180px;
    transition: opacity 1s ease-out;
    opacity: 0;
    height: 0;
    overflow: hidden;
}

.VisualExample_demo_answer.show {
    opacity: 1;
    height: auto;
}


table.MultiplicationTable {
    border-collapse: collapse;
    margin: 25px;
}

.MultiplicationTable td, .MultiplicationTable th {
    border: 1px solid #999;
    padding: 0.5rem;
    min-width: 20px;
    text-align: center;
}

.active {
    background: rgba(251, 138, 56, .91);
}


